export default {
  routers: [
    {
      path: '/user/login',
      name: 'userLogin',
      component: () => import('@/view/user/UserLogin.vue')
    },
    {
      path: '/user/reg',
      name: 'userReg',
      component: () => import('@/view/user/UserReg.vue')
    },
    {
      path: '/user/main/:username',
      name: 'userMain',
      component: () => import('@/view/user/UserMain.vue'),
      children: [
        {
          path: 'userInfo',
          name: 'userMainInfo',
          component: () => import('@/view/user/update/UserMainInfo.vue'),
        },
        {
          path: 'password',
          name: 'userMainPasswordUpdate',
          component: () => import('@/view/user/update/PasswordUpadate.vue'),
          meta: {
            isAuth: true
          }
        },
        {
          path: 'update',
          name: 'userMainUpdate',
          component: () => import('@/view/user/update/UserInfoUpdate.vue'),
          meta: {
            isAuth: true
          }
        },
        {
          path: 'phone',
          name: 'userPhoneUpdate',
          component: () => import('@/view/user/update/PhoneUpdate.vue'),
          meta: {
            isAuth: true
          }
        },
        {
          path: 'email',
          name: 'userEmailUpdate',
          component: () => import('@/view/user/update/EmailUpdate.vue'),
          meta: {
            isAuth: true
          }
        }
      ],
    }
  ]
};
