// 引入tools
import tools from '@/js/tools';
//引入Vue核心库
import Vue from 'vue';
//引入Vuex
import Vuex from 'vuex';
//应用Vuex插件
Vue.use(Vuex);

const USER_TOKEN_locality = 'user_token_data';
const USER_Login = 'user_login';

function getUser() {
  let user = localStorage.getItem(USER_TOKEN_locality);
  if (user) {
    return JSON.parse(user);
  } else {
    return {};
  }
}
function getIsLogin() {
  let Login = localStorage.getItem(USER_Login);
  if (Login) {
    return Login;
  } else {
    return false;
  }
}

function romvuser() {
  localStorage.removeItem(USER_TOKEN_locality);
  localStorage.setItem(USER_Login, false);
}
function setUser(user) {
  // console.log(user);
  localStorage.setItem(USER_TOKEN_locality, JSON.stringify(user));
  localStorage.setItem(USER_Login, true);
}

//准备actions对象——响应组件中用户的动作
const actions = {
  updatedUser(countext) {
    return new Promise((resolve, reject) => {
      try {
        tools.ajax(
          '/user/auth/getUserInfo',
          {},
          (data) => {
            if (data.success) {
              countext.commit('loginUser', data);
            } else {
              countext.commit('logoutUser');
            }
            resolve();
          },
          true
        );
      } catch (ex) {
        reject(ex);
      }
    });
  }
};
//准备mutations对象——修改state中的数据
const mutations = {
  loginUser(state, data) {
    state.user.tbUser = data.tbUser;
    state.user.tbUserInfo = data.tbUserInfo;
    state.user.userOtherInfo = data.userOtherInfo;
    setUser(state.user);
  },
  logoutUser() {
    romvuser();
    state.user = getUser();
  }
};
//准备state对象——保存具体的数据
const state = {
  user: getUser(),
  isLogin: getIsLogin()
};

//创建并暴露store
export default new Vuex.Store({
  actions,
  mutations,
  state
});
